/* COMMON STYLE */

body{
	font-family: 'Karla', sans-serif;
	font-size: 14px;
	line-height: 25px;
	color: #a7a7a7;
	letter-spacing: normal;
	font-weight: normal;
	overflow-x:hidden; 
	background-color: #fff;
}
.kt-row-flex{
	box-sizing: border-box;
	display: flex;
	-webkit-flex-wrap: wrap;
	-ms-flex-wrap: wrap;
	flex-wrap: wrap;
	display: -webkit-box;
	display: -webkit-flex;
	display: -ms-flexbox;
}
.kt-row-flex:before,
.kt-row-flex:after{
	display: none;
}
.kt-row-flex .kt-col-flex{
	-webkit-box-flex: 1;
    -webkit-flex-grow: 1;
    -ms-flex-positive: 1;
    flex-grow: 1;
    display: flex;
    -webkit-flex-direction: column;
    -ms-flex-direction: column;
    flex-direction: column;
}

h1,h2,h3,h4,h5,h6{
	font-family: 'Lato', sans-serif;
	font-weight: 700;
	line-height: normal;
	margin-top: 0;
	margin-bottom: 15px;
	color: #313131;
}
h1{
	font-size: 48px;
}
h2{
	font-size: 36px;
}
h3{
	font-size: 30px;
}
h4{
	font-size: 24px;
}
h4{
	font-size: 18px;
}
h6{
	font-size: 16px;
}
p{
	margin-bottom: 20px;
}

a{
	color: #454545;
    -webkit-transition: all 0.3s ease 0s;
    -o-transition: all 0.3s ease 0s;
    transition: all 0.3s ease 0s;
    text-decoration: none;
}
a:hover,
a:active,
a:focus{
	color: inherit;
	outline: none;
	text-decoration: none;
}
input[type="text"],
input[type="password"],
input[type="email"],
input[type="number"],
textarea{
	background-color: #f6f6f6;
	padding: 12px 15px;
	border: none;
	border-radius: 0;

}
input[type="text"]:focus,
input[type="password"]:focus,
input[type="email"]:focus,
input[type="number"]:focus,
textarea:focus{
	outline: none;
}
img{
	max-width: 100%;
	height: auto;
}
.button,
.submit{
	border: 2px solid #505050;
	font-family: 'Lato',sans-serif;
	text-transform: uppercase;
	color: #505050;
	font-size: 14px;
	display: inline-block;
	padding: 11px 45px;
	-webkit-border-radius: 3px;
	-moz-border-radius: 3px;
	-ms-border-radius: 3px;
	-o-border-radius: 3px;
	border-radius: 3px;
	background-color: transparent;
	font-family: 'Lato',sans-serif;
	font-weight: 600;
}
.button.primary{
	background-color: #505050;
	color: #fff;
}
.scroll_top {
    width: 40px;
    height: 40px;
    position: fixed;
    z-index: 9999;
    right: 10px;
    bottom: 32px;
    display: none;
    color: #fff;
    text-align: center;
    line-height: 40px;
    background-color: #222;
}
table {
    border-collapse: collapse;
    width: 100%;
    margin-bottom: 10px;
}
table, th, td {
    border: 1px solid #eaeaea;
    
}
th, td{
    padding: 5px;
}
.alignnone {
    margin: 5px 20px 20px 0;
}
.aligncenter,
div.aligncenter {
    display: block;
    margin: 5px auto 5px auto;
}

.alignright {
    float:right;
    margin: 5px 0 20px 20px;
}

.alignleft {
    float: left;
    margin: 5px 20px 20px 0;
}

a img.alignright {
    float: right;
    margin: 5px 0 20px 20px;
}

a img.alignnone {
    margin: 5px 20px 20px 0;
}

a img.alignleft {
    float: left;
    margin: 5px 20px 20px 0;
}

a img.aligncenter {
    display: block;
    margin-left: auto;
    margin-right: auto
}

img{
    height: auto;
}

.text-center{  text-align: center; }
.wp-caption {
    background: #fff;
    border: 1px solid #f0f0f0;
    max-width: 100%; /* Image does not overflow the content area */
    padding: 5px 3px 5px;
    text-align: center;
    margin: 0;
}

.wp-caption.alignnone {
    margin: 5px 20px 20px 0;
    clear: both;
}

.wp-caption.alignleft {
    margin: 5px 20px 20px 0;
}

.wp-caption.alignright {
    margin: 5px 0 20px 20px;
}

.wp-caption img {
    border: 0 none;
    height: auto;
    margin: 0;
    max-width: 100%;
    padding: 0;
    width: auto;
}

.wp-caption p.wp-caption-text {
    font-size: 12px;
    line-height: 17px;
    margin: 0;
    padding: 10px 4px 5px;
    margin: 0!important;
}

.gallery-caption {
    color: #707070;
    color: rgba(51, 51, 51, 0.7);
    display: block;
    font-family: "Noto Sans", sans-serif;
    font-size: 12px;
    font-size: 1.2rem;
    line-height: 1.5;
    padding: 0.5em 0;
}

.bypostauthor {
    color: inherit;
}
/* TAB */
.tab-container {
    position: relative;
}
.tab-container .tab-panel {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    opacity: 0;
    visibility: hidden;
}
.tab-container .active {
    opacity: 1;
    visibility: inherit;
    position: inherit;
    -vendor-animation-duration: 0.3s;
    -vendor-animation-delay: 1s;
    -vendor-animation-iteration-count: infinite;
}

/* OWL */
.owl-controls .owl-prev,
.owl-controls .owl-next{
	display: inline-block;
	width: 30px;
	height: 30px;
	line-height: 30px;
	background-color: #fff;
	color: #333;
	text-align: center;
}
.owl-controls .owl-prev:hover,
.owl-controls .owl-next:hover{
	color: inherit;
}

.nav-center-center .owl-next, 
.nav-center-center .owl-prev{
    position: absolute;
    top: 50%;
    -webkit-transform: translateY(-50%);
    -ms-transform: translateY(-50%);
    -o-transform: translateY(-50%);
    transform: translateY(-50%);
    -webkit-transition: all 0.3s ease 0s;
    -o-transition: all 0.3s ease 0s;
    transition: all 0.3s ease 0s;
    opacity: 0;
    filter: alpha(opacity=0);
    visibility: hidden;
}
.nav-center-center .owl-next{
	right: -30px;
}
.nav-center-center .owl-prev{
	left: -30px;
}
.nav-center-center:hover .owl-next{
	right: 0;
	opacity: 1;
    filter: alpha(opacity=100);
    visibility: inherit;
}
.nav-center-center:hover .owl-prev{
	left: 0;
	opacity: 1;
    filter: alpha(opacity=100);
    visibility: inherit;
}

