}
/* MOBILE MENU */
.box-mobile-menu {
    position: fixed;
    top: 0;
    left: -320px;
    width: 320px;
    bottom: 0;
    background-color: #fff;
    z-index: 100;
    -webkit-transition: all 0.3s ease 0s;
    -o-transition: all 0.3s ease 0s;
    transition: all 0.3s ease 0s;
    z-index: 1002;
}

.box-mobile-menu .box-inner {
    padding: 15px;
    overflow: hidden;
}
.box-mobile-menu .close-menu {
    width: 35px;
    height: 35px;
    font-size: 14px;
