}
.product-item .product-name a{
	color: #555555;
}
.product-item .product-name a:hover{
	color: inherit;
}
.product-item .price{
	font-size: 16px;
	color: #b9b9b9;
	font-family: 'Lato',sans-serif;
}
.product-item .price ins{
	text-decoration: none;
}
.product-item .price del{
