}
.product-item .rating{
	color: #ebe5d9;
}
.product-item .rating .active{
	color: #f1ce8b;
}
.product-item .thumb{
	position: relative;
}
.product-item .thumb .product-image{
	display: block;
	position: relative;
	overflow: hidden;
}
.product-item .thumb .product-image img{
	width: 100%;
	transition: all 0.5s ease-out 0s;
}
.product-item .thumb:hover .product-image img{
	    transform: scale(1.15);
}
.product-item .thumb .product-image:before{
	position: absolute;
    top: 50%;
    left: 50%;
    width: 0;
    height: 0;
    opacity: 0;
    content: "";
    background: rgba(255,255,255,0.2);
    transition: all 0.5s ease-out 0s;
    z-index: 2;
}

.product-item:hover .thumb .product-image:before {
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    opacity: 1;
}
.product-item .group-buttons{
	position: absolute;
	left: 0;
	width: 100%;
	bottom: 50px;
	text-align: center;
	z-index: 3;
}
.product-item .add_to_cart_button,
.product-item .button.yith-wcqv-button,
.product-item .added_to_cart {
	border: none;
	padding: 0;
	width: 52px;
	height: 52px;
	font-size: 0;
	background-color: #fff;
	display: inline-block;
	line-height: 52px;
	-webkit-border-radius: 0;
	-moz-border-radius: 0;
	-ms-border-radius: 0;
	-o-border-radius: 0;
	border-radius: 0;
	vertical-align: middle;
	-webkit-transition: all .5s ease;
	transition: all .5s ease;
	-webkit-transform: scale(0,1);
	transform: scale(0,1);
	opacity: 0;
	font-size: 0;
	float: initial;
}
.product-item .add_to_cart_button.loading:before{
	font-family: 'FontAwesome';
    content: "\f021";
    -webkit-animation: fa-spin 1s infinite steps(8);
    animation: fa-spin 1s infinite steps(8);
    display: inline-block;
    font-size: 18px;
}
.product-item .group-buttons .added{
	display: none;
}


.product-item:hover .add_to_cart_button,
.product-item:hover .button.yith-wcqv-button,
.product-item:hover .added_to_cart{
	opacity: 1;
    -webkit-transform: scale(1);
    transform: scale(1);
}
.product-item:hover .added_to_cart:before{
	font-family: 'FontAwesome';
	font-size: 18px;
	content: "\f00c";
}
.product-item .add_to_cart_button:before{
	font-family: 'Linearicons-Free';
	content: "\e82e";
	font-size: 23px;
}
.product-item .button.yith-wcqv-button:before{
	font-family: 'Linearicons-Free';
	content: "\e86f";
	font-size: 20px;
}
.product-item .yith-wcwl-add-to-wishlist{
	display: inline-block;
	float: initial;
	vertical-align: middle;
}
.product-item .yith-wcwl-add-to-wishlist .ajax-loading{
	position: absolute;
	top: 50%;
	left: 50%;
	-webkit-transform: translate(-50%,-50%);
	-ms-transform: translate(-50%,-50%);
	-o-transform: translate(-50%,-50%);
	transform: translate(-50%,-50%);
	margin: 0;
}
.product-item .yith-wcwl-add-to-wishlist>div{
	display: inline-block;
	position: relative;
	font-size: 0;
}
.product-item .yith-wcwl-add-to-wishlist>div a{
	border: none;
	padding: 0;
	width: 52px;
	height: 52px;
	font-size: 0;
	background-color: #fff;
	display: inline-block;
	line-height: 52px;
	-webkit-border-radius: 0;
	-moz-border-radius: 0;
	-ms-border-radius: 0;
	-o-border-radius: 0;
	border-radius: 0;
	vertical-align: middle;
	-webkit-transition: all .5s ease;
	transition: all .5s ease;
	-webkit-transform: scale(0,1);
	transform: scale(0,1);
	opacity: 0;
	font-size: 0;
	display: inline-block;
}
.product-item:hover .yith-wcwl-add-to-wishlist>div a{
	opacity: 1;
    -webkit-transform: scale(1);
    transform: scale(1);
}
.product-item .yith-wcwl-add-to-wishlist>div a:before{
	font-family: 'FontAwesome';
	font-size: 18px;
}
.product-item .yith-wcwl-add-button a:before{
