.product-category a{
	display: block;
	position: relative;
	overflow: hidden;
}

.product-category a:before {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 0;
    height: 0;
    opacity: 0;
    content: "";
    background: rgba(255,255,255,0.2);
    transition: all 0.5s ease-out 0s;
    z-index: 2;
}
.product-category:hover a:before:before {
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    opacity: 1;
}

.product-category a img{
	width: 100%;
