/* TAB */
.tab-container {
    position: relative;
}
.tab-container .tab-panel {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    opacity: 0;
    visibility: hidden;
}
.tab-container .tab-panel.active {
    opacity: 1;
    visibility: inherit;
    position: inherit;
    -vendor-animation-duration: 0.3s;
    -vendor-animation-delay: 1s;
    -vendor-animation-iteration-count: infinite;
}
.kt-tabs .nav-tab {
	display: table;
	margin: 0 auto;
	padding: 0;
}
.kt-tabs .nav-tab li{
	list-style: none;
	display: inline-block;
	float: left;
	color: #e8e6e6;
	font-size: 24px;
	font-weight: 700;
}
.kt-tabs .nav-tab li:after{
	content: '//';
	display: inline-block;
	padding: 0 15px;
	letter-spacing: -15px;
	font-weight: normal;
}
.kt-tabs .nav-tab li:last-child:after{
	display: none;
}
.kt-tabs .nav-tab li a{
	color: #e8e6e6;
	text-transform: uppercase;
	
}
.kt-tabs .nav-tab li:hover a,
.kt-tabs .nav-tab li.active a{
	color: #444;
}
.tab-head:before,
.tab-head:after{
	content: '';
	display: table;
}
.tab-head:after{
	clear: both;
}
