/* BANNER */
.bannereffect-1{
	display: block;
	position: relative;
	overflow: hidden;
}
.bannereffect-1:before {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(255,255,255,0.2);
    content: '';
    -webkit-transition: -webkit-transform .6s;
    transition: transform .6s;
    -webkit-transform: scale3d(1.9,1.4,1) rotate3d(0,0,1,45deg) translate3d(0,100%,0);
    transform: scale3d(1.9,1.4,1) rotate3d(0,0,1,135deg) translate3d(0,100%,0);
    opacity: 0;
    filter: alpha(opacity=0);
    visibility: hidden;
}
.bannereffect-1:hover:before {
    -webkit-transform: scale3d(1.9,1.4,1) rotate3d(0,0,1,45deg) translate3d(0,-100%,0);
    transform: scale3d(1.9,1.4,1) rotate3d(0,0,1,135deg) translate3d(0,-100%,0);
	opacity: 1;
	filter: alpha(opacity=100);
	visibility: initial;
}
.bannereffect-2{
	display: block;
	position: relative;
}
.bannereffect-2:before {
    content: '';
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    -webkit-transition: all .6s ease-in-out;
    -moz-transition: all .6s ease-in-out;
    -ms-transition: all .6s ease-in-out;
    -o-transition: all .6s ease-in-out;
    transition: all .6s ease-in-out;
}
.bannereffect-2:after {
    content: '';
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    -webkit-transition: all .6s ease-in-out;
    -moz-transition: all .6s ease-in-out;
    -ms-transition: all .6s ease-in-out;
    -o-transition: all .6s ease-in-out;
    transition: all .6s ease-in-out;
}
.bannereffect-2:hover:before {
    right: 50%;
    left: 50%;
    background: rgba(255,255,255,0.2);
}
.bannereffect-2:hover:after {
    top: 50%;
    bottom: 50%;
    background: rgba(255,255,255,0.2);
}

.bannereffect-3{
	display: block;
	position: relative;
}
.bannereffect-3:before,
.bannereffect-3:after{
	content: '';
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	bottom: 0;
	background-color: rgba(0,0,0,0.4);
	-webkit-transition: all 0.8s ease 0s;
	-o-transition: all 0.8s ease 0s;
	transition: all 0.8s ease 0s;
	opacity: 0;
}
.bannereffect-3:after{
	left: inherit;
	right: 0;
	width: 0%;
	background-color: rgba(0,0,0,0.3);
	-webkit-transition: all 0.1s ease 0s;
	-o-transition: all 0.1s ease 0s;
	transition: all 0.1s ease 0s;
}
.bannereffect-3:hover:before{
	width: 0%;
	opacity: 1;
}
.bannereffect-3:hover:after{
	width: 100%;
}
.bannereffect-4{
	display: block;
	position: relative;
}
.bannereffect-4:before{
    content: "";
    position: absolute;
    top: 25px;
    left: 25px;
    bottom: 25px;
    right: 25px;
    z-index: 9;
    border-top: 1px solid rgba(255, 255, 255, 0.8);
    border-bottom: 1px solid rgba(255, 255, 255, 0.8);
    transform: scale(0, 1);
}
.bannereffect-4:after {
    content: "";
    position: absolute;
    top: 25px;
    left: 25px;
    bottom: 25px;
    right: 25px;
    z-index: 9;
    border-left: 1px solid rgba(255, 255, 255, 0.8);
    border-right: 1px solid rgba(255, 255, 255, 0.8);
    transform: scale(1, 0);
}
.bannereffect-4:hover:before, 
.bannereffect-4:hover:after{
    transform: scale(1);
    -webkit-transition: all 0.3s ease-out 0s;
    -moz-transition: all 0.3s ease-out 0s;
    -o-transition: all 0.3s ease-out 0s;
    transition: all 0.3s ease-out 0s;
}
