}
.slide-home1 .owl-prev .fa,
.slide-home1 .owl-next .fa,
.slide-home2 .owl-prev .fa,
.slide-home2 .owl-next .fa,
.slide-home6 .owl-prev .fa,
.slide-home6 .owl-next .fa{
